.about {
    margin-top: 235px;

    .title {
        font-family: "MulishSemiBold";
        font-size: 46px;
        font-weight: 600;

        .grey {
            color: #A49590;
        }

        .green {
            color: #58BF16;
        }
    }

    .text {
        font-size: 20px;
        margin-top: 30px;
        padding: 40px 40px 40px 0;
        position: relative;
        width: 600px;

        .backGrey {
            background-color: #EDEDED;
            border-radius: 15px;
            height: 100%;
            top: 50%;
            position: absolute;
            transform: translate(-50%, -50%);
            width: 200%;
            z-index: -1;
        }
    }

    .img {
        display: flex;
        justify-content: flex-end;
        margin-top: 60px;
        position: relative;

        .left {
            position: absolute;
            right: 65%;

            .lineTop {
                background: #58BF16;
                height: 127px;
                width: 3px;
            }

            .lineBottom {
                background: #58BF16;
                height: 3px;
                width: 162px;
            }
        }

        .bottom {
            position: absolute;
            right: 20%;
            top: 110%;

            .lineTop {
                background: #58BF16;
                height: 127px;
                width: 3px;
            }

            .lineBottom {
                background: #58BF16;
                height: 3px;
                position: absolute;
                right: 0;
                width: 123px;
            }
        }
    }
}

@media only screen and (max-width: 1070px) {
    .about {
        margin-top: 507px;
    }
}

@media only screen and (max-width: 964px) {
    .about {
        margin-top: 507px;

        .title {
            text-align: center;
        }

        .text {
            margin: 50px auto;
            width: 70%;
            padding: 20px;
            margin: 30px auto;

            .backGrey {
                left: 50%;
                width: 120%;
            }
        }

        .img {
            justify-content: center;

            img {
                width: 100%;
            }
    
            .left {
                display: none;
            }
    
            .bottom {
                display: none;
            }
        }
    }
}

@media only screen and (max-width: 510px) {
    .about {
        margin-top: 400px;
        
      .title {
        font-size: 30px;
      }

      .text {
        font-size: 15px;
      }
    }
}